.offcanvas{
    .offcanvas-header{
        .cross-logo{
            i{
                font-size: 32px;
                transition: 200ms;
                color: red;
                &:hover{
                    transform: scale(1.2);
                }
            }
        }
    }
    .offcanvas-body{
        ul{
            margin-top: 20px;
            li{
                font-size: 20px;
                padding: 10px 0;
                list-style-type: none;
                color: #16336D;
                font-weight: bold;
            }
        }
    }
}